body {
  margin: 0;
  font-family: "Lato", 'Segoe UI', 'Roboto', 'Oxygen', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Lato";
  font-weight: 900;
}

code {
  font-family: Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.vertical-padding {
  height: 30px;
}

.home-container {
  display: flex;
  justify-content: center;
}

.home-box {
  padding: 30px;
  text-transform: uppercase;
}

.home-box:hover {
  color: grey;
}

.navbar-light .navbar-nav .nav-link {
  color: red;
}